import { Component, OnInit } from '@angular/core';
import { CollectionService } from '../../shared/services/collection.service';
import { Collection } from '../../shared/models/collection';

@Component({
    styles: [` 
        .flexthis{
            display: -webkit-flex;
            display: -ms-flexbox;
            display: flex;
        
            -webkit-flex-wrap: wrap;
            -ms-flex-wrap: wrap;
            flex-wrap: wrap;
        }
        .box {
            display: -webkit-flex;
            display: -ms-flexbox;
            display: flex;
        } 
    `],
    templateUrl: 'collection-list.component.html'
})
export class CollectionListComponent implements OnInit{
    collections: Collection[];
    attachments = new Array;
    constructor(
        private service: CollectionService
    ){}
    
    ngOnInit(){
        this.service.getCollections()
            .subscribe(res => {
                this.collections = res;
                for(let i = 0; i < res.length ; i++){
                    let collectionid = res[i].id;
                        this.service.getBagsFromCollection(collectionid)
                            .subscribe(bags => {
                            let bagid = bags[0].id;
                                this.service.getBagWithAttachments(bagid)
                                .subscribe(data => {
                                    let attachmenturl = data[0].url;
                                    console.log(attachmenturl);
                                    this.attachments.push(attachmenturl);
                                    }
                                )
                            }
                        )    
                    }                  
                 }
            )
        }
}


